.side{
    width: 5vw;
    height: 100vh;
    top: 0px;
    background-color: #003b57;
    position: fixed;
    display: block;
    transition: 0.4s ease-in-out;

}
.side-bar{
    width: 15vw;
    position: absolute;
    top: 0px;
    background-color: #003b57;
    height: 100vh;
    position: fixed;
    display: block;
    transition: 0.4s ease-in-out;

}
.side-container>a{
    text-decoration: none;
}
/*Sidebar Active links*/
.Navitem-act{
    border-left:10px solid #246b8e ;
    width: 97%;
    display: flex;
    align-items: center;   
    justify-content: center;
    
}
/*Sidebar Container*/
.side-container{
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow-x:hidden ;   
}
.side-container>*{
    margin: 0.5vh;
}
.Sidebar-links p{text-decoration: none;font-size: 1.3rem;color: aliceblue;font-weight: bold;margin-left: 2vw;}
.Sidebar-links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
}
.sidebar-logo{color:White;height:10vh;display: flex;flex-direction: row;align-items: center;justify-content:center;background-color: #00293e;width: 100%;}
.infinity-logo{font-size: 4rem;color: burlywood;}
.side .side-container p,.side .side-container h1{display: none;}
.side .Sidebar-links{margin: 2vh 0px;} 
.sidebar-logo{user-select: none;}

.Sidebar-links:hover,.Sidebar-links>p:hover{
    color: aquamarine;
}