.full-content{
    margin: 0px 1vw;
    display: flex;

}
.left-content{
    flex-direction: column;
    flex: 1;
    margin-right: 0.2vw;
    
}
.right-content{
    flex-direction: column;
    flex: 1;
    margin-left: 0.2vw;
   
}
@media (max-width:400px) {
    .full-content{
        display: block;
        width: 100%;
        margin-left: 5vw;
    }
    
}