.bodyy{
    display: grid;
    place-items: center;
    height: 100vh;
    margin-left:15vw;
}
.main{
    width: 50vw;
    height: 50vh;
}
.bodyy>*{
    display: block;
    width: 40vw;
}
.logo>*{
    display: block;
    font-size: 10vw;
    margin: 0px;
}
.Buttons>p{
    text-decoration: underline;
       
}
.Buttons>p:hover{
    transform: scale(1.1);
}
.Buttons{
    display: flex;
    width: 20vw;
    font-size:2vw;
    justify-content: space-evenly;
    padding: 0px 3vw;
    cursor: pointer;
}

