.form{
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
    z-index: 10;
    position: absolute;
    top: 0vh; 
}
.form-container{
    position: relative;
    display: flex;
    background-color: white;
    width: 40vw;
    height: 50vh;
    align-items: center;
    flex-direction: column;
    border-radius:20px;
    box-shadow: -2px -2px 20px 1px #272728;
}
.form-body{
    width: 100%;
    height: 50%;
}
.footer-form{  
    width: 100%;
    position: relative;
}
.login-button{
    width: 10vw;
    border-radius:12px;
    background-color: rgb(56, 56, 85);
    color: white;
    font-weight: bold;
    font-size: 1.3vw;
    padding: 10px;
    cursor: pointer;
}
.headerr{
    width: 100%;
    background-color:rgb(112, 205, 248);
    border-radius: 10px;
    align-items: flex-start;
    top: 0px;
}
.header-head{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 0px 2vw;
    cursor: pointer;
}
.footer-div{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    cursor: pointer;
    margin: 0px 2vw;
}
.forgot-password{
    font-size:0.9rem;
    text-decoration: underline;
}
.form-body>{
    display: block;
}
.error-message{
    color: red;
    position: relative;
    bottom: 10%;
}