.topmenu{
    display: flex;
    height: 10vh;
    background-color: rgb(233, 233, 233);
    align-items: center;
    justify-content: space-between;
    padding: 0.5vh 1vw;
    margin-bottom: 10vh;
    border-bottom: 1px solid gray;
}
.topmenu-lefticons{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 20vw;
    cursor: pointer;
}