.table{
    width: 90%;
    min-height: 10vh;
    overflow: hidden;
    margin-bottom: 3vh;
    user-select: none;
    box-shadow: 1px 1px 20px 1px grey;
}
.table-inner{
    margin: 1vw;   
}
.table-top{
    width: 100%;
    border-bottom: 1px solid grey;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
.table-top>h3{color: rgb(63 81 181);}
.close-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color:#aad3e7;
    font: 1.5em bolder ;
    cursor: pointer;
}
